body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
   min-width: 850px;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: row;
  min-height: 100vh;
  width: 100vw;
  min-width: 850px;
  overflow-x: hidden;

}

.App-graph {
  height: 40vmin;
  width: 100%;
  pointer-events: none;
}


.app-controls {
  width: 10vw;
  background-color: #282c34;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.control{
    width:100%;
}
input{
width:100%;
}
.controls {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  box-sizing: border-box;
}

.control_form{
    width:15vw;
    align-self: center;
    padding: 0 0.5rem;
    box-sizing: border-box;
    
}


/* CSS */
button {
  background-color: #0095ff;
  border: 1px solid transparent;
  border-radius: 3px;
  box-shadow: rgba(255, 255, 255, .4) 0 1px 0 0 inset;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-family: -apple-system,system-ui,"Segoe UI","Liberation Sans",sans-serif;
  font-size: 13px;
  font-weight: 400;
  line-height: 1.15385;
  margin: 0.5rem;
  outline: none;
  padding: 8px .8em;
  position: relative;
  text-align: center;
  text-decoration: none;
  -ms-user-select: none;
      user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: baseline;
  white-space: nowrap;
}

button:hover,
button:focus {
  background-color: #07c;
}

button:focus {
  box-shadow: 0 0 0 4px rgba(0, 149, 255, .15);
}

button:active {
  background-color: #0064bd;
  box-shadow: none;
}

.graph-bar {
  display: flex;
  flex-direction: column;
  width:100%;
}
.graph-bar_netgains {
  background-color: white;
}
.graph-bar_aportations {
  background-color: #176BA0;
}

.graph-bar_interest{
    background-color: #19AADE;
}

p {
  margin: 0;
  padding: 0;
  font-size: 0.8rem;
  color: black;
}
.graph-bars {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
}
.graph-bars_container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}
.graph-bars_bar_label{
  border-radius: 10%;
  background-color: greenyellow;
  border: solid 2px yellow;
  margin: 5px;
  width:50%;
  
}
h3 {
  margin: 0;
  padding: 0.5rem;
}
.graph-area {
  background-color: #f3f3f3;
  width: 100%;
  height: 80%;
  display: flex;
  border: solid 3px #ccc;
  box-sizing: border-box;

  padding: 1rem;
  padding-left: 0;
}
.graph-area_no_Data {
  flex-direction: column;
  justify-content: space-around;
  padding: 5rem;
}
.graph-area_xaxis_bars {
  display: flex;
  flex-direction: column;
  width: 90%;
  height: 100%;
  box-sizing: border-box;
}
.graph-area_yaxis{
  width: 10%;
}
.graph-area_bars {
  height: 95%;
}
.graph-area_titles {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
}
.graph-area_xaxis{
  height:5%;
}
.xaxis {
  border-top: solid 3px black;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 100%;
  background-color: #f3f3f3;
}

.yaxis{
    text-align: center;
    flex-direction: column;
    display: flex;
    justify-content: flex-end;
    border-right: solid 3px black;
    width:10%;
}
.yaxis_label{
    border-top: solid 3px black;
    box-sizing: border-box;
    height: 5%;
}
.yaxis_ticks{
    height: 95%;
    display: flex;
    flex-direction: column;
justify-content: flex-end;
}
.yaxis_tick{
box-sizing: border-box;
display: flex;
justify-content: flex-end;

}
.yaxis_line{
    background-color: black;
    width: 15%;
    height: 2px;
    margin-right: 0;
    margin-top:0;
}
.graph{
    width:85vw;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    box-sizing: border-box;

}
